<script>
import {Table, TableColumn} from 'element-ui'
import axios from "axios";
import {baseUrl} from "../../../config/index.json";
import DatePicker from 'vue2-datepicker';
import 'vue2-datepicker/index.css';
export default {
  name: 'light-table',
  components: {
    [Table.name]: Table,
    [TableColumn.name]: TableColumn,
    DatePicker
  },
  data() {
    return {
      time2: new Date(),
      selected: [], // Array reference
      options: [
      ],
      selected1: [], // Array reference
      options1: [
      ],
      menus:[],
      rows: 1,
      perPage: 1,
      currentPage: 1,
      index: 0,
      right_t: false,
      data_main: [],
      title:'',
      titleru:'',
      titleen:'',
      order:'',
      isFooter:false,
      url:'',
      isButton:true,
      main_id:'',
      address:'',
      email_address:'',
      phone_number:'',
      telegram:'',
      instagram:'',
      facebook:'',
      contact:[],
      imageUrl:[],
      myImages:[],
      files_name:[],
      description:'',
      descriptionru:'',
      descriptionen:'',
      short_description:'',
      short_descriptionru:'',
      short_descriptionen:'',
      is_country:false,
      is_europe:false,
      is_world:false,
      is_published:true
    };
  },
  methods: {
    trySubmitFile(e) {
      this.imageUrl=[]
      for (let i = 0; i < e.target.files.length; i++) {
        this.files_name.push(e.target.files[i])
      }
      var files = Array.from(e.target.files);
      console.log(typeof (files))
      files.forEach((file) => {
        this.myImages.push(file);
        var reader = new FileReader();
        var vm = this;
        reader.onload = function(e) {
          vm.imageUrl.push(e.target.result);
        };
        reader.readAsDataURL(file);
      });
    },
    rigt_side() {
      this.right_t = !this.right_t
      this.isButton=false
    },
    updatePage(item) {
      this.data_main = Users[item - 1]
    },
    createOz(){

      const form = new FormData();
      form.append("cover_image", this.myImages[0]);
      form.append("order", this.order);
      form.append("description", this.description);
      form.append("short_description", this.short_description);
      form.append("title", this.title);
      form.append("publish_date", new Date(this.time2).toISOString());
      form.append("is_published", this.is_published);
      axios.post(`${baseUrl}news/`,form,
        {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('token')}`,
            'Accept-Language':'oz'
          }
        })
        .then(res=>{
          this.title=''
          this.description=''
          this.order=''
          this.myImages=[]
          this.$toast("Ma'lumotlar o'zbek tilida kiritildi", {
            timeout: 2000,
            type:"success"
          });
          //ru
          form.append("cover", this.myImages[0]);
          form.append("inside_image", this.myImages[1]);
          form.append("image", this.myImages[2]);
          form.append("order", this.order);
          form.append("description", this.descriptionru);
          form.append("short_description", this.short_descriptionru);
          form.append("title", this.titleru);
          form.append("common_programs", this.selected);
          form.append("country", this.selected1);
          axios.patch(`${baseUrl}news/${res.data.id}/`,form,
            {
              headers: {
                'Authorization': `Bearer ${localStorage.getItem('token')}`,
                'Accept-Language':'ru'
              }
            })
            .then(()=>{

              this.$toast("Ma'lumotlar rus tilida kiritildi", {
                timeout: 2000,
                type:"success"
              });
              //en
              form.append("cover", this.myImages[0]);
              form.append("inside_image", this.myImages[1]);
              form.append("image", this.myImages[2]);
              form.append("order", this.order);
              form.append("description", this.descriptionen);
              form.append("short_description", this.short_descriptionen);
              form.append("title", this.titleen);
              form.append("common_programs", this.selected);
              form.append("country", this.selected1);
              axios.patch(`${baseUrl}news/${res.data.id}/`,form,
                {
                  headers: {
                    'Authorization': `Bearer ${localStorage.getItem('token')}`,
                    'Accept-Language':'kar'
                  }
                })
                .then(()=>{

                  this.$toast("Ma'lumotlar barcha tillarda kiritildi", {
                    timeout: 2000,
                    type:"success"
                  });
                })
            })
        })
      .catch(()=>{
        this.$toast("Ma'lumotlar kiritishda xatolik bor", {
          timeout: 2000,
          type:"error"
        });
      })
    },

  },
  mounted() {
    axios.get(`${baseUrl}news/${this.$route.params.id}`,{
      headers:{
        'Accept-Language':'oz'
      }
    })
      .then(res=>{
        this.menus=res.data
        this.imageUrl.push(this.menus.cover_image)
        this.title=this.menus.title
        this.description=this.menus.description
        this.short_description=this.menus.short_description
        this.order=this.menus.order
        this.url=this.menus.url
        this.isFooter=this.menus.is_footer
        this.time2=new Date(this.menus.publish_date)
        console.log(res)
      })
    axios.get(`${baseUrl}news/${this.$route.params.id}`,{
      headers:{
        'Accept-Language':'ru'
      }
    })
      .then(res=>{
        this.menus=res.data
        this.descriptionru=this.menus.description
        this.short_descriptionru=this.menus.short_description
        this.titleru=this.menus.title
        console.log(res)
      })
    axios.get(`${baseUrl}news/${this.$route.params.id}`,{
      headers:{
        'Accept-Language':'kar'
      }
    })
      .then(res=>{
        this.menus=res.data
        this.descriptionen=this.menus.description
        this.short_descriptionen=this.menus.short_description
        this.titleen=this.menus.title

        console.log(res)
      })
  }
}

</script>
